"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, RefreshCw } from "lucide-react"
import { Badge } from "@/components/ui/badge"
import { Checkbox } from "@/components/ui/checkbox"
import { Label } from "@/components/ui/label"

export default function VerificarPermissoesPage() {
  const [user, setUser] = useState<any>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [permissoes, setPermissoes] = useState<string[]>([])
  const [todasPermissoes, setTodasPermissoes] = useState<string[]>([
    "USUARIOS_VISUALIZAR",
    "USUARIOS_CRIAR",
    "USUARIOS_EDITAR",
    "USUARIOS_EXCLUIR",
    "PRODUTOS_VISUALIZAR",
    "PRODUTOS_CRIAR",
    "PRODUTOS_EDITAR",
    "PRODUTOS_EXCLUIR",
    "VENDAS_VISUALIZAR",
    "VENDAS_CRIAR",
    "VENDAS_EDITAR",
    "VENDAS_EXCLUIR",
    "FILA_VISUALIZAR",
    "FILA_CRIAR",
    "FILA_EDITAR",
    "FILA_EXCLUIR",
    "ADMIN",
  ])
  const [permissoesTestadas, setPermissoesTestadas] = useState<Record<string, boolean>>({})

  // Carregar dados do usuário ao montar o componente
  useEffect(() => {
    const storedUser = localStorage.getItem("user")
    if (storedUser) {
      try {
        const parsedUser = JSON.parse(storedUser)
        setUser(parsedUser)

        // Extrair permissões do usuário
        if (parsedUser.permissoes) {
          if (Array.isArray(parsedUser.permissoes)) {
            setPermissoes(parsedUser.permissoes)
          } else if (typeof parsedUser.permissoes === "string") {
            try {
              const parsedPermissoes = JSON.parse(parsedUser.permissoes)
              if (Array.isArray(parsedPermissoes)) {
                setPermissoes(parsedPermissoes)
              }
            } catch (err) {
              console.error("Erro ao analisar permissões:", err)
              setPermissoes([])
            }
          }
        }
      } catch (err) {
        console.error("Erro ao analisar dados do usuário:", err)
        setError("Erro ao analisar dados do usuário")
      }
    } else {
      setError("Usuário não encontrado no localStorage")
    }
  }, [])

  // Função para verificar se o usuário tem uma permissão específica
  const verificarPermissao = (permissao: string) => {
    if (!user) return false

    // Verificar se é administrador
    if (user.perfil === "Administrador" || user.perfil === "ADMIN") {
      return true
    }

    // Verificar permissões específicas
    return permissoes.includes(permissao)
  }

  // Função para testar todas as permissões
  const testarTodasPermissoes = () => {
    setLoading(true)
    const resultados: Record<string, boolean> = {}

    todasPermissoes.forEach((permissao) => {
      resultados[permissao] = verificarPermissao(permissao)
    })

    setPermissoesTestadas(resultados)
    setLoading(false)
  }

  return (
    <div className="container mx-auto py-8">
      <h1 className="text-3xl font-bold mb-6">Verificar Permissões</h1>

      <div className="grid gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Dados do Usuário</CardTitle>
            <CardDescription>Informações sobre o usuário atual</CardDescription>
          </CardHeader>
          <CardContent>
            {user ? (
              <div className="space-y-4">
                <div>
                  <span className="font-semibold">Nome:</span> {user.nome}
                </div>
                <div>
                  <span className="font-semibold">Username:</span> {user.username}
                </div>
                <div>
                  <span className="font-semibold">Perfil:</span> {user.perfil}
                </div>
                <div>
                  <span className="font-semibold">Permissões:</span>
                  <div className="flex flex-wrap gap-2 mt-2">
                    {permissoes.length > 0 ? (
                      permissoes.map((permissao, index) => (
                        <Badge key={index} variant="outline">
                          {permissao}
                        </Badge>
                      ))
                    ) : (
                      <span className="text-gray-500">Nenhuma permissão específica</span>
                    )}
                  </div>
                </div>
                <div>
                  <span className="font-semibold">É Administrador:</span>{" "}
                  {user.perfil === "Administrador" || user.perfil === "ADMIN" ? (
                    <span className="text-green-600">Sim</span>
                  ) : (
                    <span className="text-red-600">Não</span>
                  )}
                </div>
              </div>
            ) : error ? (
              <Alert variant="destructive">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            ) : (
              <div>Carregando dados do usuário...</div>
            )}
          </CardContent>
          <CardFooter>
            <Button onClick={() => window.location.reload()}>Recarregar</Button>
          </CardFooter>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Testar Permissões</CardTitle>
            <CardDescription>Verifique quais permissões o usuário atual possui</CardDescription>
          </CardHeader>
          <CardContent>
            <Button onClick={testarTodasPermissoes} disabled={loading || !user}>
              {loading ? (
                <>
                  <RefreshCw className="mr-2 h-4 w-4 animate-spin" /> Testando...
                </>
              ) : (
                "Testar Todas as Permissões"
              )}
            </Button>

            {Object.keys(permissoesTestadas).length > 0 && (
              <div className="mt-6">
                <h3 className="text-lg font-semibold mb-4">Resultados:</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {Object.entries(permissoesTestadas).map(([permissao, resultado]) => (
                    <div key={permissao} className="flex items-center space-x-2">
                      <Checkbox id={permissao} checked={resultado} disabled />
                      <Label htmlFor={permissao} className={resultado ? "text-green-600 font-medium" : "text-red-600"}>
                        {permissao}
                      </Label>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Verificação Manual</CardTitle>
            <CardDescription>Verifique uma permissão específica</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <h3 className="text-lg font-semibold mb-2">Permissões Comuns:</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                  <Button
                    variant="outline"
                    onClick={() => alert(verificarPermissao("USUARIOS_VISUALIZAR") ? "Permitido" : "Negado")}
                  >
                    Verificar USUARIOS_VISUALIZAR
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => alert(verificarPermissao("USUARIOS_CRIAR") ? "Permitido" : "Negado")}
                  >
                    Verificar USUARIOS_CRIAR
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => alert(verificarPermissao("PRODUTOS_VISUALIZAR") ? "Permitido" : "Negado")}
                  >
                    Verificar PRODUTOS_VISUALIZAR
                  </Button>
                  <Button variant="outline" onClick={() => alert(verificarPermissao("ADMIN") ? "Permitido" : "Negado")}>
                    Verificar ADMIN
                  </Button>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
